<template>
    <div>
        <ul>
            <li v-for="(stock,index) in stocks" :key="index">
                {{ stock.name }}:{{ stock.qty }}*{{ stock.price }}+{{ stock.fee }}
                ={{stock.qty*stock.price+stock.fee}}
            </li>
        </ul>
      {{ total }} 
    </div>
</template>

<script>
export default {
    data() {
        return {
            stocks: [
                {
                    name: "國泰金",
                    qty: 20,
                    price: 66.5,
                    fee: 1    
                },
                {
                    name: "萬海",
                    qty: 10,
                    price: 168,
                    fee: 2 
                },
                {
                    name: "台積電",
                    qty: 2,
                    price: 682,
                    fee: 1
                }
            ]
        }
    },
    computed : {
        total: function() { 
                // let sum = 0;
             var reducePlus = this.stocks.reduce(
                 (sum, stock)=> sum + (stock.qty*stock.price+stock.fee),0                    
            ); 
            // var reducePlus = this.stocks.reduce(function(sum, stock){ 
            //         return sum + (stock.qty*stock.price+stock.fee)
            //         },0
            // );
            // var reducePlus = this.stocks.reduce(function(accumulator, stock){
            // // 分別為前一個回傳值, 目前值, 當前索引值            
            //     return accumulator + (stock.qty*stock.price+stock.fee);  // 與前一個值相加
            // }, 0); 
           
           return reducePlus;
           
        }
    }
}
</script>

